html {
  height: 100%; }

body {
  height: 100%;
  @include font-smoothing;
  color: $color-darkblue;
  min-width: 320px; }

b, strong {
  @include f(b); }

em, i {
  @include f(li); }

.out {
  overflow: hidden; }

.dark-bg {
  background: #1E3054; }

