.header {
  position: relative;
  z-index: 1;
  background: $color-darkblue;
  color: #FFF;
  box-sizing: border-box;
  padding: 9px 48px 12px;
  min-height: 110px;
  box-shadow: 0 0 20px 0 $color-darkblue;
  @include clr;
  @include md; }


.header__logo {
  // margin-right: 45px
  margin-right: -87px;
  float: left; }

.header__content {
  float: left;
  margin-left: 132px; }

.header__right {
  float: right;
  margin-top: 16px; }

.header-call {
  display: inline-block;
  @include f(b, 14);
  margin-top: 13px;
  text-transform: uppercase;
  color: $color-yellow;
  text-decoration: none;
  line-height: 1;
  &:first-child {
    margin-right: 110px; }
  .icon-phone {
    font-size: 22px;
    color: #5F6778;
    position: relative;
    top: 1px; } }

.header-call__number {
  margin-left: 17px;
  @include f(be, 27); }

.main-logo {
  display: block;
  position: relative;
  height: 1em;
  width: em(174,178);
  font-size: 178/2 +px;
  background: img-url('logo@2x.png') no-repeat center;
  background-size: contain;
  text-decoration: none;
  color: inherit;
  &_beta {
    &:before {
      position: absolute;
      top: 0;
      right: 5px;
      content: "beta";
      @include f(b, 10);
      height: 14px;
      line-height: 14px;
      box-sizing: border-box;
      padding: 0 3px;
      background: $color-yellow;
      transform: skewX(-10deg);
      color: $color-darkblue; } } }


.main-menu {
  font-size: 0;
  margin-top: 14px; }

.main-menu__link {
  display: inline-block;
  color: white;
  @include f(l, 14);
  line-height: 30px;
  margin-right: 15px;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;
  &:last-child {
    margin-right: 0; }
  &:after {
    visibility: hidden;
    opacity: 0;
    transform: scale(1, 0);
    transform-origin: top left;
    content: "";
    position: absolute;
    top: 50%;
    margin-top: em(12,14);
    left: 0;
    height: 3px;
    width: 100%;
    background: whitesmoke;
    transition-property: opacity, visibility;
    @include trans; }
  &:hover,
  &.is-active {
    &:after {
      visibility: visible;
      opacity: 1;
      transform: scale(1); } }
  &.is-active {
    @include f(b); } }

.global-search {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  display: none; }

.global-search__input {
  height: 32px;
  width: 100px;
  background: none;
  box-sizing: border-box;
  padding: 0 15px 0 36px;
  border-radius: 14px;
  color: #A1A1A1;
  @include f(l, 12);
  text-transform: uppercase;
  &:focus {
    color: black;
    text-transform: none;
    background: white;
    width: 230px;
    @include placeholder {
      color: #B8BDC6; }
    & + .global-search__icon {
      color: #D8DDE6; } } }

.global-search__icon {
  position: absolute;
  top: 50%;
  left: 12px;
  font-size: 17px;
  margin-top: -0.5em;
  pointer-events: none;
  color: white; }


.lang {
  cursor: pointer;
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin-left: 16px;
  &.is-active {
    .lang__list {
      visibility: visible; }
    .icon {
      visibility: hidden; } } }

.lang__item {
  display: block;
  @include f(l, 12);
  text-transform: uppercase;
  line-height: 1;
  cursor: pointer;
  white-space: nowrap;
  color: $color-lightgrey;
  img {
    width: 13px;
    height: 10px;
    margin-right: 4px; }
  [class^="flag-"] {
    position: relative;
    margin-right: 4px; }
  .icon {
    position: relative;
    vertical-align: middle;
    font-size: 4px;
    margin-left: 4px;
    top: -1px; } }

.lang__list {
  visibility: hidden;
  position: absolute;
  top: -9px;
  left: -10px;
  padding: 9px 10px;
  width: auto;
  background: #20335B;
  .lang__item {
    &:not(:first-child) {
      margin-top: 12px; } } }
