.l {
  max-width: 960px;
  box-sizing: content-box;
  margin: auto;
  padding: 0 20px; }

.row {
  &:before,
  &:after {
    content: "";
    display: table; }
  &:after {
    clear: both; } }

$colsizes: 5, 10, 15, 20, 25, 30, 35, 40, 45, 50, 60, 70, 75, 80, 100;
.col {
  float: left;
  box-sizing: border-box;
  width: 100%;
  @each $size in $colsizes {
    &-#{$size} {
      width: $size + %; } } }

.l-main-page {
  @include clr;
  @include r($bp2) {
    .l-main-page__left,
    .l-main-page__right {
      float: none;
      width: auto;
      margin: 0;
      max-width: none; }
    .l-main-page__right {
      margin-top: 50px; } } }

.l-main-page__left {
  float: left;
  width: 100%;
  max-width: 310px;
  margin-right: -310px; }

.l-main-page__right {
  float: right;
  margin-left: 360px;
  max-width: 550px; }

.l-news-page {
  @include clr; }

.l-news-page__left {
  float: left;
  width: 330px;
  margin-right: -330px;
  box-sizing: border-box;
  padding-right: 20px; }

.l-news-page__right {
  float: right;
  margin-left: 350px;
  max-width: 590px; }

.l-article {
  margin-bottom: 75px;
  @include clr; }

.l-article__aside {
  float: left;
  width: 100%;
  width: percentage(180/960); }

.l-article__content {
  float: right;
  width: percentage(720/960); }
